}

body {
  font-family: Poppins, sans-serif;
}

h1, h2, h3, h4, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

address {
  font-style: normal;
}

li {
  list-style: none;
}

img {
  display: block;
}

a {
  text-decoration: none;
}

button {
  all: unset;
  cursor: pointer;
  display: inline-block;
}

.list {
  list-style: none;
}

.link {
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

nav {
  z-index: 99;
  width: 100%;
