  justify-content: center;
  display: flex;
}

.movies-list, .series-list, .people-list {
  flex-wrap: wrap;
  gap: 20px;
  padding: 0;
  list-style: none;
  display: flex;
}

.movies-item, .series-item, .person-item {
  box-sizing: border-box;
  max-width: calc(25% - 20px);
  border-radius: 5px;
  flex: calc(25% - 20px);
